.tab {
  padding: 1em;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.tabscontainer {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tab-content {
  font-size: 0.75rem;
  max-height: 0;
  transition: max-height 0.35s;
  background-color: white;
  color: #333333;
  border-radius: 0px 0px 4px 4px;
}

.tab input:checked ~ .tab-content {
  max-height: 100vh;
}

.tab input:checked + label {
  color: rgba(255, 255, 255);
  border-radius: 12px 12px 0px 0px;
}

.tab label {
  font-size: 1em;
  padding: 1em;
  background: linear-gradient(135deg, #ffc963 0%, #ffac53 100%);
  color: white;
  border-radius: 12px;
}

.tab label::after {
  float: right;
  right: 0;
  top: 0;
  display: block;
  text-align: center;
  justify-content: center;
  width: 1rem;
  height: 1em;
  transition: all 0.35s;
}

.tab input + label::after {
  content: url(/static/media/Down-ArrowHead.449faaea.svg);
  -webkit-filter: invert(50%);
  filter: invert(50%);
}

.tab input:checked + label::after {
  content: url(/static/media/Up-ArrowHead.33985cc1.svg);
  -webkit-filter: invert(0%);
  filter: invert(0%);
}

